import React, { FC, useContext } from 'react';
import { graphql } from 'gatsby';
import Grid from '@material-ui/core/Grid';
import { GlobalContext } from '@src/provider';
import { default as Layout } from '@src/components/layout';
import { default as ArticleMaker } from '@src/components/ArticleMaker';

type IProps = {
    data: {
        allMarkdownRemark: any;
    };
};
const CategoriesTemplate: FC<IProps> = (props) => {
    const { data } = props;
    const { state } = useContext(GlobalContext);
    const { categoriesTemplateData } = state;
    const { components } = categoriesTemplateData;
    const { article } = components;
    return (
        <Layout title="categories">
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <ArticleMaker
                        articleList={data.allMarkdownRemark.edges}
                        enablePagination={article.enablePagination}
                        limit={article.limit}
                        showAuthor={article.showAuthor}
                    />
                </Grid>
            </Grid>
        </Layout>
    );
};
export default CategoriesTemplate;

export const pageQuery = graphql`
    query($category: String!) {
        allMarkdownRemark(filter: { frontmatter: { published: { eq: true }, category: { eq: $category } } }) {
            edges {
                node {
                    id
                    excerpt
                    frontmatter {
                        date(formatString: "YYYY/MM/DD")
                        title
                        tags
                        category
                        bannerPhoto {
                            childImageSharp {
                                fluid {
                                    src
                                    originalName
                                }
                            }
                        }
                        authorPhoto {
                            childImageSharp {
                                fluid {
                                    src
                                    originalName
                                }
                            }
                        }
                        author
                    }
                    fields {
                        slug
                    }
                }
            }
        }
    }
`;
